<template>
  <div class="content" v-html="parsedMarkdown" />
</template>

<script>
import marked from "marked";
export default {
  name: "Content",
  props: {
    input: {
      type: String,
      required: true,
      default: ""
    }
  },
  computed: {
    parsedMarkdown() {
      return marked(this.input, { sanitize: true });
    }
  }
};
</script>

<style scoped lang="scss"></style>
